import { SHOW_POP_UP, CLOSE_POP_UP, SHOW_HOTSPOT } from './reducersKeys'
const initialState = { isShowPopUp: false, data: null, showHotspot: null, index: null, showAll: false, hotspotId: null }
export default function reducer(state = initialState, action) {
    switch (action.type) {
        case SHOW_POP_UP: {
            return { ...state, isShowPopUp: true, data: action.payload }
        }
        case CLOSE_POP_UP: {
            return { ...state, isShowPopUp: false, data: null }
        }
        case SHOW_HOTSPOT: {
            return { ...state, ...action.payload}
        }
        default:
            return { ...state }
    }

}