import React, { Component } from 'react';
import { getWallID } from './utils'
import Route from './Route';
class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            wall_id: getWallID()
        }
    }
    render() {
        const { wall_id } = this.state;
        console.log(wall_id)
        return <Route wallID={wall_id} />
    }
}

export default Main;