import React, { Component } from 'react';
import { connect } from 'react-redux';
import { showHotspot } from '../../../../actions/themeActions';
import ProductImg from './Media/productImg'
import ReactHtmlParser from 'react-html-parser'; 

const URL = `https://app.taggbox.com/commerce/`;

class PopUpMedia extends Component {
    state={prdImgLoaded:false, noImg:false}


    render() {
        const { item, ugc_products, wall, showHotspot } = this.props;
        const { prdImgLoaded, noImg } = this.state;
        const ugcSetting = item.ugc_personalizaion.UgcSetting;
        return <>


            <div className="tb_shop_cont">
                <div className={`tb_pro_title tb_pro_title_user tb-cTSfont-${ugcSetting.feature_font_varient}`}>{ReactHtmlParser(ugcSetting.feature_text)}</div>
                <div className="ts_modal_product tb_modal_pro">
                    {ugc_products.map((product, index) => <div className="tb_pro_item" key={index}>

                        <div className={`tb_pro_wrapp product-item-${index}`} onMouseEnter={()=> showHotspot({showHotspot:true, index, hotspotId: product.id})} onMouseLeave={() => showHotspot({showHotspot:false, index, hotspotId: product.id})}>
                         <ProductImg imgUrl={product.UgcProduct.product_image}/>
                            {(item.ugc_personalizaion && ugcSetting && product.UgcProduct.product_title) ?
                                <div className={`ugc-product-title tb-cTSfont-${ugcSetting.title_font_varient}`}> {product.UgcProduct.product_title}</div> : null}

                            {product.UgcProduct.product_price > 0 || product.UgcProduct.product_discount > 0 ? <div className="ugc-product-price">
                                <div className={`tb_pro_price tb-cTSfont-${ugcSetting.price_font_varient}`}> {product.UgcProduct.price_currency_symbol}{" "}{product.UgcProduct.product_discount > 0 ? product.UgcProduct.product_discount : product.UgcProduct.product_price}</div>
                            </div> : null}
                            {product.UgcProduct.product_discount > 0 ?
                                <div className={`tb_pro_discount_price  tb-cTSfont-${ugcSetting.discount_font_varient}`} >{product.UgcProduct.price_currency_symbol}{" "}{product.UgcProduct.product_price}</div> : null}
                            <div className="ugc-product-link"><a href={product.UgcProduct.product_url} className={`tb-cTSfont-${ugcSetting.button_font_varient}`} target={wall && wall.User && wall.User.id !=`102810` ? "_blank" : ''}>{ugcSetting.buy_now_text}</a></div>
                        </div>
                    </div>
                    )}</div>
            </div>
        </>
    }
}


const mapStateToProps = state => ({
  })
  const mapDispatchToProps = dispatch => {
    return {
     
     showHotspot: (data) => dispatch(showHotspot(data))
    }
  }

export default connect(mapStateToProps, mapDispatchToProps)(PopUpMedia);
