export const convertObjectToArray = (obj) => {
    let result = Object.keys(obj).map((key) => obj[key]);
    return result;
};

export const getWallID = () => {
    // const parsed = queryString.parse(window.location.search);
    // if (parsed.wall_id) return parsed.wall_id;
    if (window.wall_Id) return window.wall_Id;
    else {
        var container = document.getElementsByClassName('taggbox-socialwall');
        for (var i = 0; i < container.length; i++) {
            if (container[i].getAttribute("data-is-load") == "0") {
                const updateData = document.getElementById(`co_${container[i].getAttribute("data-render-id")}`)
                if (updateData) {
                    updateData.setAttribute("data-is-load", "1")
                }
                return container[i].getAttribute("data-wall-id")
            }
        }
    }
}

export const getRenderContainer = () => {
    //const parsed = queryString.parse(window.location.search);
    if (window.wall_Id) return document.getElementById('taggShopRoot');
    else return extendsGetRenderContainer()
}

const extendsGetRenderContainer = () => {
    try {
        var container = document.getElementsByClassName('taggbox-socialwall');
        for (var i = 0; i < container.length; i++) {
            if (container[i].getAttribute("data-is-load") == "0") {
                return document.getElementById(container[i].getAttribute("data-render-id"))
            }
        }
    }
    catch (ex) {
        return document.getElementById('taggShopRoot');
    }
}




const getDevidedValue = (value1, value2) => value1 / value2;
const postWidthAdjustSpacing = (cardNumber, rowCard, windowWidth, postWidth) => {
    let spacing = null; let width = null; let adjustSpacing = null;
    spacing = (windowWidth) % postWidth;
    adjustSpacing = spacing / rowCard;
    if (cardNumber >= 1) width = (postWidth + adjustSpacing);
    else width = (postWidth - adjustSpacing);
    return { spacing, width, adjustSpacing }
}

export const THEME_WIDTH_ADJUSTMENT_MODERN = (windowWidth, wall, postData) => {
    let cardNumber = null; let adjustWidth = null;

    if (wall.Personalization.commerceTheme == '4') {
        var mini_N_Post = parseInt( windowWidth / wall.Personalization.minimumPostWidth);
        cardNumber = postData.length >= mini_N_Post ? mini_N_Post : postData.length

        if(windowWidth > 768 && wall.ThemeRule.numberOfCoumn > 0){
        adjustWidth = (windowWidth / wall.ThemeRule.numberOfCoumn) - 0.9;
        }
        else if(windowWidth > 768 &&  wall.ThemeRule.numberOfCoumn == 0){
            adjustWidth = (windowWidth / cardNumber) - 0.9;
        }
        else if(windowWidth < 768 && wall.ThemeRule.mobileColumn==0){
            adjustWidth = (windowWidth / cardNumber) - 0.9;
        }
        else{
            adjustWidth = (windowWidth / wall.ThemeRule.mobileColumn) - 0.9;
        }
        return { adjustWidth };
    }

    // if (wall.Personalization.commerceTheme == '4' && wall.ThemeRule.numberOfCoumn > 0) {
    //     if(windowWidth > 768 || wall.ThemeRule.mobileColumn==0){
    //     adjustWidth = (windowWidth / wall.ThemeRule.numberOfCoumn) - 0.9;
    //     }else{
    //         adjustWidth = (windowWidth / wall.ThemeRule.mobileColumn) - 0.9;
    //     }
    //     return { adjustWidth };
    // }
    // if (wall.Personalization.commerceTheme == '4' && wall.ThemeRule.numberOfCoumn == 0) {
    //     var mini_N_Post = parseInt( windowWidth / wall.Personalization.minimumPostWidth);
    //         cardNumber = postData.length >= mini_N_Post ? mini_N_Post : postData.length
    //     if(windowWidth > 768 || wall.ThemeRule.mobileColumn==0){
    //         adjustWidth = (windowWidth / cardNumber) - 0.9;
    //         }else{
    //             adjustWidth = (windowWidth / wall.ThemeRule.mobileColumn) - 0.9;
    //         }
    //         return { adjustWidth };
    // }
 
    if (parseInt(windowWidth) > parseInt(wall.Personalization.minimumPostWidth)) {
        cardNumber = getDevidedValue(windowWidth, wall.Personalization.minimumPostWidth);
        const { width } = postWidthAdjustSpacing(cardNumber, Math.trunc(cardNumber), windowWidth, wall.Personalization.minimumPostWidth);
        adjustWidth = width;
        adjustWidth = adjustWidth - 0.8;
        return { adjustWidth };
    }
    else return { adjustWidth: windowWidth }
}

export const IS_SCREEN_SHOT = window.location.href.includes("screenshot") ? false : true

export const fontColorIfWhite = (white, color, defaultColor, cardColor) => {
    if (cardColor === white) return white == color ? defaultColor : color;
    else if (cardColor === defaultColor) return defaultColor == color ? white : color;
    return white == color ? defaultColor : color;
}



export const randomNumber = (min, max, freeAdsPosition) => {
    try {
        var position = freeAdsPosition; let count = 3;
        for (var i = 0; i < 10; i++) {
            count = parseInt(count) + 9 + parseInt(i);
            if (!position.includes(count)) position.push(count);
        }
        return position;
    }
    catch (ex) {
        return freeAdsPosition
    }
}

export const numPost = () => {
    const rootWidth = document.querySelector('.taggshopContainer') ? document.querySelector('.taggshopContainer').clientWidth:2000;
    if (rootWidth > 2000) { return 5; }
    else if (rootWidth <= 2000 && rootWidth > 1024) { return 4; }
    else if (rootWidth <= 1024 && rootWidth > 600) { return 3; }
    else if (rootWidth <= 600 && rootWidth > 480) { return 2; }
    else { return 1; }
}





export const findNetworkDataFromWebFilters = (networkID, webFilters) => {
    try {
        if (webFilters && webFilters.length > 0) {
            let network = webFilters.filter((item) => item.Network.id == networkID)
            if (network && network[0] && network[0].Network) return (network[0].Network);
            else return {
                color: "#00aced",
                description: "Twitter",
                icon: "fa-twitter",
                id: 1,
                name: "Twitter",
                status: 1
            }
        }
        else return {
            color: "#00aced",
            description: "Twitter",
            icon: "fa-twitter",
            id: 1,
            name: "Twitter",
            status: 1
        }
    }
    catch (ex) {
        return {
            color: "#00aced",
            description: "Twitter",
            icon: "fa-twitter",
            id: 1,
            name: "Twitter",
            status: 1
        };
    }

}

export const getTotalRowsForGalleryTheme = (postData, ColumnCount) => {
    var newPostdata = [],
        i = 0, n = postData.length, j = 0, devide;
    var secondArr = ColumnCount == 5 ? 7 : ColumnCount == 4 ? 5 : ColumnCount == 3 ? 3 : ColumnCount == 2 ? 1 : 5;
    while (i < n) {
        devide = j % 2 == 0 ? ColumnCount : secondArr;
        newPostdata.push(postData.slice(i, i += devide));
        j++;

    }
    return newPostdata;
}

export const getTotalRows = (postData, wall) => {
    if(wall && wall.Personalization && wall.Personalization.commerceTheme && (wall.Personalization.commerceTheme == `53` || wall.Personalization.commerceTheme == `50` || wall.Personalization.commerceTheme == `4` || wall.Personalization.commerceTheme == `16` || wall.Personalization.commerceTheme == `47` || wall.Personalization.commerceTheme == `55`)){
    const ColumnCount =wall.ThemeRule.numberOfCoumn;
    const galleryThm = 3;
    const windowWidth = document.querySelector('.taggshopContainer') ? document.querySelector('.taggshopContainer').clientWidth:'';
    const fitRow = wall.Personalization.fitRow;
    var newPostdata = []
    if (wall.Personalization.commerceTheme == `53`) {
        var clm = ColumnCount ? ColumnCount : 1
        var i = 0, n = postData.length;
        while (i < n) { newPostdata.push(postData.slice(i, i += clm)); }
        return newPostdata;
    }
    
    else if (wall.Personalization.commerceTheme == `50`) {
        var i = 0, n = postData.length, devide;
        while (i < n) { 
            devide = ( i == 0 && wall.UgcSettings.onsite_status && (!window.isEditor || checkPreview())  )? galleryThm-1 : galleryThm;
            newPostdata.push(postData.slice(i, i += devide)); 
        }
        return  newPostdata.length < 9 ? Array(10).fill(newPostdata).flat() : newPostdata;
    }


   else if (wall.Personalization.commerceTheme == `4`) {
      var mobCrdNumber = wall.ThemeRule.mobileColumn
     var  cardNumber = windowWidth > 737 ? ColumnCount: mobCrdNumber;
     if(ColumnCount == 0 && fitRow==0){
        return newPostdata = postData;
     }
     else if(ColumnCount > 0 && fitRow==0){
        return newPostdata = postData;
     }
   else{
        if(cardNumber == 0 && fitRow==1 ){
            var mini_N_Post = parseInt( windowWidth / wall.Personalization.minimumPostWidth);
            cardNumber = postData.length >= mini_N_Post ? mini_N_Post : postData.length
        }
        var inputArray = postData;
        var result = inputArray.reduce((resultArray, item, index) => {
            const chunkIndex = Math.floor(index / cardNumber)
            if (!resultArray[chunkIndex]) {
                resultArray[chunkIndex] = [] // start a new chunk
            }
            resultArray[chunkIndex].push(item)
            return resultArray
        }, [])
        var arrNew = [];
        for (let i = 0; i < result.length; i++) {
            if (result[i].length == cardNumber) {
                for (let j = 0; j < result[i].length; j++) {
                    arrNew.push(result[i][j])
                }
            }
        }
        return newPostdata = arrNew;
       }
    }
    else if (wall.Personalization.commerceTheme == `16`) {
        var rows = wall.ThemeRule.multiRow ? wall.ThemeRule.multiRow : 1;
        var i = 0, n = postData.length;
        while (i < n) { newPostdata.push(postData.slice(i, i += rows)); }
        return newPostdata.length < 12 ? Array(15-newPostdata.length).fill(newPostdata).flat() : newPostdata;
    }
    else if (wall.Personalization.commerceTheme == `47`) {
        return postData.length < 12 ? Array(15-postData.length).fill(postData).flat() : postData;
    }
    else if (wall.Personalization.commerceTheme == `55`) {
        return postData.length < 12 ? Array(15-postData.length).fill(postData).flat() : postData;
    }
}

    else {
        return newPostdata;
    }

}

