var windowHeight = window.innerHeight;
var rootHeight = document.querySelector('.taggshopContainer').clientHeight;
var marginFromTop = ((windowHeight-70) - rootHeight) / 2;
document.querySelector('.taggshopContainer').style.marginTop= marginFromTop > 0 ? marginFromTop+`px`: `0px`;
