export const checkPreview = () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.preview) return true;
    else return false;
}

export const getPostWidth = (windowWidth, ColumnCount) => {
    let cardWidth = ''; let inWidth = ''; let largeDiv = ''; let smallDiv = '';
    if (ColumnCount == 3) { if (windowWidth > 320) { inWidth = 100 / 3; cardWidth = 100 / 1; largeDiv = inWidth * 2; smallDiv = 100 - largeDiv; } else { cardWidth = 100 / 1; largeDiv = 100; smallDiv = 100 } }
    else if (ColumnCount == 5) { if (windowWidth > 767) { inWidth = 100 / 4; cardWidth = 100 / 2; largeDiv = inWidth * 2; smallDiv = 100 - largeDiv; } else { cardWidth = 100 / 2; largeDiv = 100; smallDiv = 100 } }
    else if (ColumnCount == 7) { if (windowWidth > 767) { inWidth = 100 / 5; cardWidth = 100 / 3; largeDiv = inWidth * 2; smallDiv = 100 - largeDiv; } else { cardWidth = 100 / 2; largeDiv = 100; smallDiv = 100 } }
    else if (ColumnCount == 9) { if (windowWidth > 767) { inWidth = 100 / 6; cardWidth = 100 / 4; largeDiv = inWidth * 2; smallDiv = 100 - largeDiv; } else { cardWidth = 100 / 2; largeDiv = 100; smallDiv = 100 } }
    else { if (windowWidth < 767) { inWidth = 100 / 3; cardWidth = 100 / 1; largeDiv = inWidth * 2; smallDiv = 100 - largeDiv; } else { cardWidth = 100 / 1; largeDiv = 100; smallDiv = 100 } }

    return { cardWidth, largeDiv, smallDiv };
}


