export const getGalleryThemePostWidth = (windowWidth, ColumnCount) => {
    let cardWidth = ''; let cardWidth2 = ''; let largeDiv = ''; let smallDiv = ''; let postCount1 = ''; let postCount2 = '';
    if (ColumnCount == 5) { cardWidth = 100 / 5; cardWidth2 = 100 / 3; largeDiv = cardWidth * 2; smallDiv = 100 - largeDiv; postCount1 = 5; postCount2 = 7; }
    else if (ColumnCount == 4) { cardWidth = 100 / 4; cardWidth2 = 100 / 2; largeDiv = cardWidth * 2; smallDiv = 100 - largeDiv; postCount1 = 4; postCount2 = 5; }
    else if (ColumnCount == 3) { cardWidth = 100 / 3; cardWidth2 = 100 / 1; largeDiv = cardWidth * 2; smallDiv = 100 - largeDiv; postCount1 = 3; postCount2 = 3; }
    else if (ColumnCount == 2) { cardWidth = 100 / 2; cardWidth2 = 100 / 1; largeDiv = cardWidth * 2; smallDiv = 100 - largeDiv; postCount1 = 2; postCount2 = 1; }
    else { cardWidth = 100 / 4; cardWidth2 = 100 / 2; largeDiv = cardWidth * 2; smallDiv = 100 - largeDiv; postCount1 = 4; postCount2 = 5; }

    return { cardWidth, cardWidth2, largeDiv, smallDiv, postCount1, postCount2 };
}


