var istaggs = isxSocialwall && isxSocialwall.getAttribute('data-filter-type') ? isxSocialwall.getAttribute('data-filter-type') : false;


export const getThemeDataWithWallID = (Id, heightEvent) => {
    return (dispatch) => {
        let updateUrl = `${GET_WIDGET_DATA_API_PATH}${Id}`
        if (istaggs) {
                updateUrl = `${GET_WIDGET_DATA_API_PATH}${Id}/${isxSocialwall.getAttribute('data-filter-type')}/${encodeURIComponent(isxSocialwall.getAttribute('data-filter-id'))}`
        }

        try {
            new HttpClient()
                .get(updateUrl)
                .then((response) => {
                    const { visitorLimit, response_code } = response.data;
                    if (parseInt(response_code) == 404) {
                        dispatch({
                            type: ERROR_RESPONSE_WITH_MESSAGE,
                            payload: response.data,
                        });
                        throw new Error(response.data.message);
                    }
                    else if (visitorLimit === 0) {
                        dispatch({ type: GET_APP_DATA, payload: response.data, heightEvent: heightEvent });
                    } else {
                        dispatch({
                            type: ERROR_RESPONSE_WITH_MESSAGE,
                            payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED", ...response.data },
                        });
                        throw new Error("");
                    }

                })
                .catch((error) => {
                    const { response } = error;
                    if (response && response.status && response.status == 404) {
                        if (parseInt(response.status) == 404) {
                            dispatch({
                                type: ERROR_RESPONSE_WITH_MESSAGE,
                                payload: response.data,
                            });
                        }
                    }
                    dispatch({
                        type: ERROR_RESPONSE_WITH_MESSAGE,
                        payload: response.data,
                    });
                });
        }
        catch (ex) {
            dispatch({
                type: ERROR_RESPONSE_WITH_MESSAGE,
                payload: { message: "", type: "VISITOR_LIMIT_EXCEEDED" },
            });
            throw new Error("");
        }
    };
};

export const languageSettings = (w_language, custom_lan_data) => {
    let cLanguage = null;
    let customLArray = null;
    let customLanguageKey = null;
    if (w_language === "custom") {
        const { getData, getKey, getCompleteData } = customLanguageParse(
            custom_lan_data
        );
        cLanguage = getCompleteData;
        customLArray = getData;
        customLanguageKey = getKey;
    }
    return {
        customLanguageData: customLArray,
        customLanguageKey: customLanguageKey,
        ...convertButtonNameLanguages(w_language, cLanguage),
    };
};

