export const getDataNextSteps = (
    wallID,
    timeStamp,
    postCount,
    networkId,
    after,
    heightEvent,
    updateInState
) => async (dispatch) => {
    let urlToAccess = `${GET_DATA_POST_APPEND}${wallID}/${timeStamp}/${postCount}/${networkId}/1/${after}`;
    if (istaggs) {
        urlToAccess = `${GET_DATA_POST_APPEND}${wallID}/${timeStamp}/${postCount}/${networkId}/1/${after}/${isxSocialwall.getAttribute('data-filter-type')}/${encodeURIComponent(isxSocialwall.getAttribute('data-filter-id'))}`
    }
    const { wall, postData } = store.getState().appData;
    const { isShowMoreLoading } = store.getState().loaderData;
    const afterUsedList = postData.afterUsedList;
    const bestFit = (wall.Personalization.widgetTheme === 4 && wall.Personalization.fitRow == 1);
    if ((!afterUsedList.includes(after) || afterUsedList.length == 0) && !isShowMoreLoading) {
        dispatch({ type: GET_THEME_SHOW_MORE_LOADER_SUCCESS, payload: true });
        themePostTracking({
            action: 1,
            wall: wallID,
            feed: "",
            post: "",
        });
        await new HttpClient()
            .get(urlToAccess)
            .then((response) => {
                dispatch({
                    type: SHOW_MORE_DATA_UPDATE, payload: {
                        after: after,
                        newData: response.data.data, filterNetworkId: networkId, appendData: {
                            after: response.data.after,
                            networkID: networkId,
                            heightEvent: heightEvent,
                        },
                        hasMoreData: {
                            [networkId]: {
                                hasMoreData: bestFit && Object.keys(response.data.data).length < postCount ? false : response.data.hasPost == 1 ? true : false,
                                after: response.data.after,
                            },
                        },
                        table_name:  (wall && wall.UserDetail && wall.UserDetail.db_table) ? wall.UserDetail.db_table : "" 
                    }
                });

                dispatch({
                    type: GET_THEME_SHOW_MORE_LOADER_SUCCESS,
                    payload: false,
                })
            })
            .catch((error) => { });
    }
};

export const filterPostDataAppendWebFilter = (
    wallID,
    timeStamp,
    postCount,
    networkId,
    after,
    heightEvent
) => {
    return (dispatch) => {
        //dispatch({ type: GET_WEB_FILTER_LOADER_SUCCESS, payload: true });
        const { wall, postData } = store.getState().appData;
        const bestFit = (wall.Personalization.widgetTheme === 4 && wall.Personalization.fitRow == 1);
        let urlToAccess = `${GET_DATA_POST_APPEND}${wallID}/${timeStamp}/${postCount}/${networkId}/1/${after}`;
        if (istaggs) {
            urlToAccess = `${GET_DATA_POST_APPEND}${wallID}/${timeStamp}/${postCount}/${networkId}/1/${after}/${isxSocialwall.getAttribute('data-filter-type')}/${encodeURIComponent(isxSocialwall.getAttribute('data-filter-id'))}`
        }
        new HttpClient()
            .get(urlToAccess)
            .then((response) => {
                const newData = response.data.data;

                dispatch({
                    type: FILTER_DATA_UPDATE,
                    payload: {
                        appendData: {
                            after: response.data.after,
                            networkID: networkId,
                            heightEvent: heightEvent,
                        },
                        filterNetworkId: networkId,
                        newData: newData,
                        hasMoreData: {
                            [networkId]: {
                                hasMoreData: bestFit && Object.keys(response.data.data).length < postCount ? false : response.data.hasPost == 1 ? true : false,
                                after: response.data.after,
                            },
                        },
                        table_name:  (wall && wall.UserDetail && wall.UserDetail.db_table) ? wall.UserDetail.db_table : "" 
                    },
                });
            })
            .catch((error) => { });
    };
};




